import types from '../../../store/mutation-types'
import headTitle from '../../../components/head-title.vue'
export default {
    name: 'account_index',
    data () {
      return {
          value: '',
          type: 'consumption',
          modal: false,
          earn: [
              {
                  name: '基本工资',
                  icon: require('../../../assets/images/default.png')
              },
              {
                  name: '公司福利',
                  icon: require('../../../assets/images/default.png')
              },
              {
                  name: '其它入账',
                  icon: require('../../../assets/images/default.png')
              }
          ],
          consumption: [
              {
                  name: '水果零食',
                  icon: require('../../../assets/images/default.png')
              },
              {
                  name: '餐饮伙食',
                  icon: require('../../../assets/images/default.png')
              },
              {
                  name: '出行旅游',
                  icon: require('../../../assets/images/default.png')
              },
              {
                  name: '网上购物',
                  icon: require('../../../assets/images/default.png')
              },
              {
                  name: '生活日常',
                  icon: require('../../../assets/images/default.png')
              },
              {
                  name: '租房水电',
                  icon: require('../../../assets/images/default.png')
              },
              {
                  name: '医疗药物',
                  icon: require('../../../assets/images/default.png')
              },
              {
                  name: '其它消费',
                  icon: require('../../../assets/images/default.png')
              }
          ]
      }
    },
    created () {
        let consumption = JSON.parse(sessionStorage.getItem('consumption'))
        let earn = JSON.parse(sessionStorage.getItem('earn'))
        console.log(earn)
        if (consumption && consumption.length > 0) {
            let arr = consumption.map((item) => {
                return {
                    name: item,
                    icon: require('../../../assets/images/default.png')
                }
            })
            this.consumption = arr
        }
        if (earn && earn.length > 0) {
            let arr = earn.map((item) => {
                return {
                    name: item,
                    icon: require('../../../assets/images/default.png')
                }
            })
            this.earn = arr
        }
        this.setNavIndex();
    },
    components: {
        headTitle
    },
    methods: {
        remove (type) {
            this.type = type
            if (this.type === 'consumption') {
                this.consumption.pop()
                this.setLocalStorage('consumption', this.consumption)
            } else {
                this.earn.pop()
                this.setLocalStorage('earn', this.earn)
            }
        },
        setLocalStorage (name, data) {
            let arr = data.map((item) => {
                return item.name
            })
            sessionStorage.setItem(name, JSON.stringify(arr))
            console.log(JSON.parse(sessionStorage.getItem(name)))
        },
        save () {
            if (this.type === 'consumption') {
                this.consumption.push({
                    name: this.value,
                    icon: require('../../../assets/images/default.png')
                })
                this.setLocalStorage('consumption', this.consumption)
            } else {
                this.earn.push({
                    name: this.value,
                    icon: require('../../../assets/images/default.png')
                })
                this.setLocalStorage('earn', this.earn)
            }
            this.value = ''
        },
        cancel () {
            this.value = ''
        },
        toDetail (name, val) {
          this.$router.push({
              name,
              query: {
                  account_type: val
              }
          })
        },
        setNavIndex () {
            this.$store.commit(types.SET_NAV_INDEX,'2')
        }
    }
}



